'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import Link from 'next/link';

export default function Register() {
    const router = useRouter();
    const [formData, setFormData] = useState({
        name: '',
        email: '',
        password: '',
        confirmPassword: '',
    });
    const [error, setError] = useState('');
    const [loading, setLoading] = useState(false);

    const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        setFormData(prev => ({ ...prev, [name]: value }));
    };

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setError('');
        setLoading(true);

        if (formData.password !== formData.confirmPassword) {
            setError('Las contraseñas no coinciden');
            setLoading(false);
            return;
        }

        try {

            router.push('/auth/login');
        } catch (err) {
            setError('Error al registrar usuario');
            console.error(err);
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="min-h-screen flex items-center justify-center bg-ccDarkGray">
            <div className="bg-ccGray p-8 rounded-xl shadow-md w-full max-w-lg border border-ccLightGray">

                <div className="flex flex-col-2 items-center justify-between px-10 py-2  text-white bg-ccLightGray rounded-xl">
                    <Link href="" className="text-xl py-2 px-4 focus:bg-white focus:text-black focus:rounded-lg ">Iniciar sesión </Link>
                    <Link href="" className="text-xl py-2 px-4 focus:bg-white focus:text-black focus:rounded-lg">Registrarse</Link>
                </div>
                
                
            </div>
        </div>
    );
}